.map {
  height: 100%;
  width: 100%;
  position: fixed;
}

.activity-viewer {
  height: 100%;
  width: 100%;
  position: relative;
}

.like-leaflet {
  z-index: 999;
  background: #fff;
  padding: 7px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  background-clip: padding-box;
  box-shadow: none;
  margin: 10px;
  border-radius: 5px;
}

.like-leaflet:hover {
  cursor: pointer;
}

.timer {
  width: 240px;
  height: 50px;
  font-size: 24px;
}

.buttons {
  position: absolute;
  width: 100%;
  bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.buttons .like-leaflet {
  padding: 0;
}

.show-timer {
  display: inline-block;
  margin: 0 16px;
  line-height: 46px;
}

.stop-button {
  display: inline-block;
  // margin-top: 8px;
}

.stop-watch {
  display: inline-block;
}
